}

.dialog {
  width: 400px;
  height: 300px;
}

.container {
  display: flex;
  position: absolute;
  top: 200px;
  left: 130px;
}

.dialog__container {
  position: absolute;
  right: 0;
  top: -200px;
}

.typeWriter {
  width: 350px;
  position: relative;
  left: -24px;
  top: -173px;
  font: 400 20px/24px 'Inter', sans-serif;
}

.continue {
  position: absolute;
  top: 200px;
  right: 152px;
}

.btn-hover {
  width: 200px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  height: 55px;
  text-align:center;
  border: none;
  background-size: 300% 100%;

  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.btn-hover:hover {
  background-position: 100% 0;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.btn-hover:focus {
  outline: none;
}

.btn-hover.color-3 {
  background-image: linear-gradient(to right, #667eea, #764ba2, #6B8DD6, #8E37D7);
  box-shadow: 0 4px 15px 0 rgba(116, 79, 168, 0.75);
}